.chartContainer {
    position: relative;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    height: 80%;
    width: 50%;
}

.lineContainer {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
}


.title {
    position: relative;
    top: 10%;
    left: 10%;
    font-family: monospace;
    font-size: x-large;
}